 import { Component, OnInit } from "@angular/core";
import { PaybudService } from "../../services/paybud.service";
import { Router } from "@angular/router";


@Component({
  selector: "app-payments",
  templateUrl: "./payments.component.html",
  styleUrls: ["./payments.component.css"]
})
export class PaymentsComponent implements OnInit {
  constructor(private services: PaybudService, private router: Router) {
    let opciones = new Audio();
    opciones.src = "../../../assets/sound/opciones.mp3";
    opciones.play();
    this.services.record("es_Es").subscribe(async e => {
      let audio = new Audio();
      audio.src = "../../../assets/sound/sound.mp3";

      if (e == "agua" || e == "luz" || e == "internet" || e == "gas") {
        audio.autoplay = true;
        await audio.play();
        this.router.navigate(["ticket"]);
      } else if (e == "anterior" || e == "atras" || e == "atrás") {
        this.router.navigate(["home"]);
      } else {
        console.log(`${e} sino`);
      }
    });
  }

  ngOnInit() {}
}