import { Component, OnInit } from "@angular/core";
import { PaybudService } from "../../services/paybud.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-ticket",
  templateUrl: "./ticket.component.html",
  styleUrls: ["./ticket.component.css"]
})
export class TicketComponent implements OnInit {
  title: "Electricidad";

  constructor(private services: PaybudService, private router: Router) {
    this.services.record("es_Es").subscribe(async e => {
      if (e === "pagar") {
        this.router.navigate(["confirmPayment"]);
        let confirmar = new Audio();
        confirmar.src = "../../../assets/sound/confirmar.mp3";
        await confirmar.play();
        /// timbre
        let audio2 = new Audio();
        audio2.src = "../../../assets/sound/sound.mp3";
        await audio2.play();
      } else if (e == "anterior" || e == "atras" || e == "atrás") {
        this.router.navigate(["payments"]);
      } else {
        console.log(`${e} sino`);
      }
    });
  }

  ngOnInit() {}
}